/* Variable declarations */
:root{
    --mainBlue: #006699;
    --mainGreen: #339966;
    --darkGray: #403B41;
    --softGray: #DEDEDE;
    --serif: 'David Libre', serif;
    --sansSerif: 'Arimo', sans-serif;
}

/* Overall styles - Starting points */
html {
    font-size: 16pt;
    background-color: var(--darkGray);
    color: var(--softGray);
    font: var(--sansSerif);
}

/* Hero section */
.hero {
    border: 5px solid var(--mainBlue);
    padding: 3%;
    margin-bottom: 5%;
}

.hero h1 {
    text-align: center;
    font: 4.25em var(--serif);
}

.intro-icons {
    text-align: center;
    width: 75%;
    margin: 0 auto;
}

.intro-icons img{
    width: 30%;
    padding: 0 4vw;
    color: var(--darkGray);
}

.project-block {
    border: 5px solid var(--mainGreen);
    width: 100vw;
}

.project-images {
    width: 90%;
}